var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bus"},[_c('page-heading',[_vm._v("Шина")]),_c('page-subheading',[_vm._v("Определение")]),_c('page-text',[_vm._v("Компьютерная ши́на (англ. computer bus) в архитектуре компьютера — соединение, служащее для передачи данных между функциональными блоками компьютера. В устройстве шины можно различить механический, электрический (физический) и логический (управляющий) уровни.")]),_c('page-text',[_vm._v("В отличие от соединения точка-точка, к шине обычно можно подключить несколько устройств по одному набору проводников. Каждая шина определяет свой набор разъёмов (соединений) для физического подключения устройств, карт и кабелей.")]),_c('page-text',[_vm._v("Компьютерные шины ранних вычислительных машин представляли собой жгуты (пучки соединительных проводов — сигнальных и питания, для компактности и удобства обслуживания увязанных вместе), реализующие параллельные электрические шины с несколькими подключениями. В современных вычислительных системах данный термин используется для любых физических механизмов, предоставляющих такую же логическую функциональность, как параллельные компьютерные шины.")]),_c('page-text',[_vm._v("Современные компьютерные шины используют как параллельные, так и последовательные соединения и могут иметь параллельные (англ. multidrop) и цепные (англ. daisy chain) топологии. В случае USB и некоторых других шин могут также использоваться хабы (концентраторы).")]),_c('page-subheading',[_vm._v("Описание шин")]),_c('page-text',[_vm._v("Шины бывают параллельными (данные переносятся потактово словами: каждый бит — отдельным проводником) и последовательными (биты данных переносятся поочерёдно по каналу, например, паре проводников).")]),_c('page-text',[_vm._v("Большинство компьютеров имеет как внутренние, так и внешние шины. Внутренняя шина подключает все внутренние компоненты компьютера к материнской плате (и, следовательно, к процессору и п